
footer{
	color: gray;
	background-color: black;
	font-size: 24px;
	height: 30px;
	padding: 3px;
}
footer .r-link{padding: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  height:auto;
  padding:0;
  margin:0;
  background-color:grey
}
.r-link{
	color: white;
	text-decoration: none;
	width: 129px;
    padding: 18px 0px;
}
nav .r-link:hover{
	color: black;
	background-color: white;
}
nav{background-color: black;
}
.nav{
	width:80%;
	margin-left: 10%;
	color: white;
	font-size: 24px;
	height: 60px;
	padding: 10px;
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
}
.nav-left{
	width: 25%;
}
.nav-left img{
	width:100%;
	margin-top: -30px
}
.nav-right{
	width: 40%;
	text-align: center;
	height: 40px;
	display: grid;
	justify-items: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}
@media (max-width: 500px){
.nav-right{display: none;
}
.nav-left{width: 100%;padding: 1.5% 0;
}
footer{font-size: 14px;
}
}

#home{
		width: 80%;
		margin: 40px 10%;
		font-family: monospace;
}
#searchbar input{
        width: 30%;
        padding: 5px 10px;
        font-size: 16px;
        margin: 40px auto;
        height: 40px;
        border:none;
        border-radius: 15px
}
#home-grid{
			display: grid;
			grid-template-columns: 1fr 1fr 1fr;
			grid-column-gap: 20px;
			grid-row-gap:20px;
}
.grid-element{
			background:black;
			text-align: left;
			color: white;
}
.img{width: 100%
}
.grid-element h1{
			margin-top: 4px;margin-bottom: 0;padding-left: 20px;
}
.grid-element p{
			margin-top: 2px;
			color: gray;padding-left: 20px;
}
.grid-element:hover{
			-webkit-transform: scale(1.2);
			        transform: scale(1.2)
}
@media (max-width: 500px){
#home{
				grid-template-columns: 1fr;
}
}

#About{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	width:80%;
	margin: 60px 10%;
	color: white;
}
.about-flex-left{
	width:50%;
}
.about-flex-right{
	width: 50%;
	background-color:black;
	display: grid;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	text-align: left;
}
.about-flex-right h1{font-size: 42px;margin-top: -5px;
}
.about-flex-right p{font-size: 18px;
}
.about-flex-right h2{font-size: 32px;color: gray;margin-top: -35px;
}
.about-flex-right-content{
	margin-left: 20px;
	margin-right: 20px
}
@media (max-width:500px){
#About h1{font-size: 30px;
}
#About h2{font-size: 24px;margin-top: -20px;
}
#About{display: block;
}
.about-flex-right , .about-flex-left{width: 100%;
}
}

#Gallery{
    width: 90%;
    margin:auto;
}
#img-popup{
    position:absolute;
    display:none;
    top: 25%;
    left: 40%;
    width: 500px;
    height: 500px;
    z-index:10;
    background: white;
    -webkit-box-shadow: 1px 3px 5px black;
            box-shadow: 1px 3px 5px black
}
#img-popup h3{
    color: black;
    text-align: right;
    margin: 5px 10px; 
    cursor: pointer
}
#img-popup img{
    width : 90%;
    height : 85%;
    margin-left: 5%;
}
.gallery-grid{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 20px;
    margin-bottom: 20px
}
.gallery-grid img{
    width: 100%;
    height: 180px;
    cursor: pointer
}
.gallery-grid img:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2)
}
